/* === –û–±—â–µ–µ –æ—Ñ–æ—Ä–º–ª–µ–Ω–∏–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã === */
body {
  margin: 0;
  background: #121212; /* –¢—ë–º–Ω—ã–π —Ñ–æ–Ω */
  font-family: -apple-system, BlinkMacSystemFont, ...;
  color: #e0e0e0; /* –°–≤–µ—Ç–ª—ã–π —Ç–µ–∫—Å—Ç */
  font-size: 13px;
  line-height: 1.4;
  letter-spacing: 0.2px;
}

/* === –û–±—ë—Ä—Ç–∫–∞ –≤—Å–µ–π —Å–∏—Å—Ç–µ–º—ã Otes === */
.otes-wrapper {
  
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  user-select: none;
  pointer-events: auto;
  -webkit-touch-callout: none;
}
.otes-wrapper * {
  user-select: none !important;
  -webkit-touch-callout: none !important;
}

/* === –ó–∞–≥–æ–ª–æ–≤–∫–∏ (–º–µ—Å—è—Ü, —Ü–µ–ª–∏) === */
.otes-title,
.otes-date-left {
  padding: 6px 22px 6px;         /* —Ç–æ—á–Ω–æ —Ç–∞–∫ –∂–µ, –∫–∞–∫ –≤ .calendar-header */
  font-size: 16px;
  font-weight: 800;
  margin: 0;                     /* —É–±–∏—Ä–∞–µ–º bottom margin */
  color: #fff;
  border-bottom: 1px solid #222;
  text-align: left;
  text-shadow: none;            /* —É–±–∏—Ä–∞–µ–º –Ω–µ–Ω—É–∂–Ω–æ–µ —Å–≤–µ—á–µ–Ω–∏–µ */
}

/* –ï—Å–ª–∏ .otes-title –Ω–µ –¥–æ–ª–∂–Ω–∞ –∏–º–µ—Ç—å –ª–∏–Ω–∏—é ‚Äî –æ—Å—Ç–∞–≤–ª—è–µ–º –∏—Å–∫–ª—é—á–µ–Ω–∏–µ */
.otes-title {
  border-bottom: none;
}

/* === –°–µ—Ç–∫–∞ –∫–∞—Ä—Ç–æ—á–µ–∫ === */
.otes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 14px;
}

/* === –û–±—â–∞—è –∫–∞—Ä—Ç–æ—á–∫–∞ === */
.otes-card {
  top: 10px;
  position: relative;
  background: rgba(30, 30, 30, 0.4);
  backdrop-filter: blur(20px);
  border-radius: 16px;
  padding: 10px;
  margin: 0 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: transform 0.2s, box-shadow 0.2s;
}
.otes-card:first-child .otes-card-title {
  border: none !important;
}

/* === –ó–∞–≥–æ–ª–æ–≤–æ–∫ –∫–∞—Ä—Ç–æ—á–∫–∏ (–Ω–∞–ø—Ä–∏–º–µ—Ä: "–ó–∞—Ä–ø–ª–∞—Ç–∞") === */
.otes-card-title {
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 0.95rem;
  padding-bottom: 3px;
  margin-bottom: -6px;
  border-bottom: 1px solid #ffffff22;
}

/* === –û—Å–Ω–æ–≤–Ω—ã–µ –∑–Ω–∞—á–µ–Ω–∏—è –≤–Ω—É—Ç—Ä–∏ –∫–∞—Ä—Ç–æ—á–µ–∫ === */
.otes-value {
  font-size: 1.4rem;
  font-weight: bold;
  margin-top: 8px;
}
.unit {
  font-size: 0.65em;
  opacity: 0.6;
  margin-left: 4px;
}

/* === –¶–≤–µ—Ç–∞: –∂—ë–ª—Ç—ã–π, —Å–∏–Ω–∏–π, –∫—Ä–∞—Å–Ω—ã–π === */
.otes-yellow { color: #FFD54F; }
.otes-blue   { color: #4FC3F7; }
.otes-red    { color: #EF5350; }

/* === –ü—Ä–æ–≥—Ä–µ—Å—Å-–±–∞—Ä—ã —Ü–µ–ª–µ–π === */
.otes-progress {
  width: 100%;
  height: 10px;
  margin-top: 8px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
}
.otes-progress-bar {
  height: 100%;
  border-radius: 8px;
  transition: width 0.4s ease-in-out;
}
.otes-progress-bar.otes-yellow { background: linear-gradient(90deg, #facc15, #fde047); }
.otes-progress-bar.otes-green  { background: linear-gradient(90deg, #22c55e, #86efac); }
.otes-progress-bar.otes-red    { background: linear-gradient(90deg, #ef4444, #f87171); }

.otes-progress-info {
  margin-top: 8px;
  font-size: 12px;
}

/* === –ó–Ω–∞—á–∫–∏ —Å—Ç–∞—Ç—É—Å–∞ (–£—Å–ø–µ—Ö, –ü—Ä–æ–≤–∞–ª –∏ —Ç.–¥.) === */
.otes-status-badge {
  margin-top: 10px;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  font-size: 0.75rem;
  font-weight: bold;
  padding: 4px 8px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.08);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}
.otes-status-badge.otes-grey {
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.4);
  padding: 3px 8px;
  font-size: 11px;
  margin-top: 6px;
}

/* === –¶–∏—Ç–∞—Ç–∞ –º–æ—Ç–∏–≤–∞—Ü–∏–∏ === */
.otes-quote {
  margin-top: 30px;
  font-size: 0.85rem;
  font-style: italic;
  color: rgba(255, 255, 255, 0.4);
  text-align: center;
  letter-spacing: 0.5px;
}

/* === –ì—Ä—É–ø–ø–∞ –∏–Ω–ø—É—Ç–æ–≤ (–¥–ª—è —Ü–µ–ª–µ–π) === */
.otes-inputs {
  margin-bottom: 24px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 16px;
}
.otes-input-group {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-top: 10px;
}
.otes-input-group label {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.7);
}
.otes-input-group input {
  padding: 8px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.07);
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.15);
  font-size: 0.9rem;
  transition: 0.2s;
}
.otes-input-group input:focus {
  border-color: #6FCF00;
  background: rgba(255, 255, 255, 0.1);
}

/* === –ù–∞–≤–∏–≥–∞—Ü–∏—è –ø–æ –º–µ—Å—è—Ü–∞–º === */
/* === –ù–∞–≤–∏–≥–∞—Ü–∏—è –ø–æ –º–µ—Å—è—Ü–∞–º (–≤ —Ç–æ–º –∂–µ –¥—É—Ö–µ, —á—Ç–æ .calendar-header) === */
.otes-date-range {
  display: flex;
  align-items: center;
  padding: 0px 16px 0px;
  font-size: 12px;
  font-weight: 500;
  border-bottom: 1px solid #222;
  gap: 0px;
  margin: 0;  /* —É–±—Ä–∞–ª–∏ –æ—Ç—Ä–∏—Ü–∞—Ç–µ–ª—å–Ω—ã–π –æ—Ç—Å—Ç—É–ø */
  color: #999;
  position: relative;
}

/* –ü–æ–¥–Ω–∏–º–∞–µ–º —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ (—Ç–µ–∫—Å—Ç + —Å—Ç—Ä–µ–ª–∫–∏) –≤–Ω—É—Ç—Ä–∏ .otes-date-range */
.otes-date-range > * {
  position: relative;
  top: -6px; /* –ø–æ–¥–Ω–∏–º–∞–µ–º —Ç–æ–ª—å–∫–æ —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ */
}

/* === –ö–Ω–æ–ø–∫–∏ —Å—Ç—Ä–µ–ª–æ–∫ ‚Äî —á–∏—Å—Ç–∏–º –∏ –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–µ–º === */
.otes-arrow-button,
.otes-nav-buttons button {
  background: none;
  border: none;
  color:#999;
  cursor: pointer;
  padding: 3px 0px;
  border-radius: 6px;
  transition: background-color 0.2s ease;
}

.otes-arrow-button {
  position: relative;
  top: -5px; /* üîª –ß—É—Ç—å-—á—É—Ç—å –æ–ø—É—Å—Ç–∏–ª–∏ —Å—Ç—Ä–µ–ª–æ—á–∫–∏ */
}

/* === –ê–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω–∞—è –ø–∞–Ω–µ–ª—å –¥–ª—è –≤–≤–æ–¥–∞ —Ü–µ–ª–µ–π === */
.otes-slide-panel {
  position: relative;
  max-height: 0;
  opacity: 0;
  transform: translateY(-10px);
  transition: 0.4s ease;
  display: flex;
  flex-direction: column;
  gap: 2px;
}
.otes-slide-panel.open {
  max-height: 1000px;
  opacity: 1;
  transform: translateY(0);
}

/* === –ö–Ω–æ–ø–∫–∞ "+" –∏ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –≤—ã–±–æ—Ä–∞ —Ü–µ–ª–µ–π === */
.otes-add-tip-btn {
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
  font-size: 18px;
  display: flex;
  align-items: center;
  gap: 6px;
}
.otes-plus-icon {
  font-size: 36px;
}

/* === –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –∏ –∑–∞—Ç–µ–º–Ω–µ–Ω–∏–µ === */
.otes-overlay {
  position: fixed;
  inset: 0;
  background: rgba(30, 30, 30, 0.822);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}
.otes-modal {
  position: relative; /* üí• –í–æ—Ç —ç—Ç–æ –¥–æ–±–∞–≤—å */
  background: rgb(30, 30, 30);
  border-radius: 20px;
  padding: 18px 26px;
  max-width: 400px;
  width: 60%;
  text-align: center;
  color: #e2e8f0;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.4);
}
.otes-modal h3 {
  font-size: 18px;
  margin-bottom: 16px;
  color: #f1f5f9;
}
.otes-modal-buttons {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.otes-modal-buttons button {
  padding: 10px 16px;
  font-size: 14px;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.05);
  color: #f1f5f9;
  cursor: pointer;
  transition: 0.2s;
  border: none;
}
.otes-modal-close {
  position: absolute;
  top: 10px;
  right: 12px;
  font-size: 20px;
  color: #fff;
  background: none;
  border: none;
  cursor: pointer;
}

/* === –ö—Ä–µ—Å—Ç–∏–∫ –Ω–∞ –∫–∞—Ä—Ç–æ—á–∫–µ –¥–ª—è —É–¥–∞–ª–µ–Ω–∏—è === */
.otes-close-card {
  position: absolute;
  top: 12px;
  right: 8px;
  background: transparent;
  border: none;
  color: #fff;
  font-size: 18px;
  cursor: pointer;
  z-index: 10;
}

/* === –ú–∏–Ω–∏-—Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –≤–Ω–∏–∑—É –∫–∞—Ä—Ç–æ—á–µ–∫ === */
.otes-stats-row {
  display: flex;
  justify-content: space-between;
  margin-top: 8px;
  padding-top: 4px;
  border-top: 1px solid rgba(255, 255, 255, 0.08);
}
.otes-stat {
  display: flex;
  flex-direction: column;
}
.otes-stat-label {
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.5);
  text-transform: uppercase;
  margin-bottom: 2px;
}
.otes-stat-value {
  font-size: 1rem;
  font-weight: 600;
  color: #e0e0e0;
}
.otes-stat-value.otes-green { color: #22c55e; }
.otes-stat-value.otes-red   { color: #ef4444; }

/* === –¶–µ–ª–∏ —Å–ø—Ä–∞–≤–∞ –≤ –∫–∞—Ä—Ç–æ—á–∫–µ === */
.otes-goal-inline {
  margin-left: auto;
  margin-top: 10px;
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 0.85rem;
  color: #f0f0f0;
  font-weight: 500;
  text-align: center;
  animation: fadeIn 0.4s ease-in-out;
}
.otes-goal-label {
  color: #aaa;
  margin-right: 6px;
}
.otes-goal-value {
  font-size: 0.9em;
  opacity: 0.6;
  font-weight: 600;
  margin-left: -4px;
}

