:root {
  --day-left-width: 45px;
  --gap: 12px;
  --day-row-padding-vertical: 8px;
  --day-row-min-height: 48px;
  --border-color: #444;
  --bg-week-block: rgba(30, 30, 30, 0.4);
}

/* === –û–±—â–∏–µ —Å—Ç–∏–ª–∏ –¥–ª—è body === */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background-color: #121212;
  color: #ffffff;
  overflow: hidden;
  height: 100vh;
}

/* === –û–±—ë—Ä—Ç–∫–∞ –≤—Å–µ–≥–æ –∫–∞–ª–µ–Ω–¥–∞—Ä—è === */
.main-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* === –ó–∞–≥–æ–ª–æ–≤–æ–∫ –∫–∞–ª–µ–Ω–¥–∞—Ä—è === */
.calendar-header {
  padding: 6px 22px 0;
  border-bottom: 1px solid #222;
}

.calendar-header h2 {
  font-size: 1rem;
  font-weight: 800;
  margin: 0;
}

.calendar-header p {
  font-size: 0.75rem;
  color: #999;
  margin-top: 0;
}

/* === –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä —Å –Ω–µ–¥–µ–ª—è–º–∏ === */
.week-container {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  padding: 0 1rem 1rem;
  box-sizing: border-box;
  max-height: calc(100vh - 80px); /* –ø–æ–¥ –∑–∞–≥–æ–ª–æ–≤–æ–∫ */
}

/* –ù–µ–¥–µ–ª—è ‚Äî –±–ª–æ–∫ —Å –∑–∞–∫—Ä—É–≥–ª–µ–Ω–∏—è–º–∏ –∏ —Ç–µ–Ω—å—é */
.week-block {
  position: relative;
  width: 100%;
  margin-top: 0.6rem;
  display: flex;
  flex-direction: column;
  background: var(--bg-week-block);
  border-radius: 1.25rem;
  padding: 0 0 0.75rem 0;
  box-shadow: 0 4px 15px #000000cc;
  box-sizing: border-box;
  height: auto;
}

/* –í–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–∞—è –ª–∏–Ω–∏—è —Å–ª–µ–≤–∞ */
.week-block::before {
  content: '';
  position: absolute;
  left: calc(var(--day-left-width) + var(--gap));
  top: 0;
  bottom: 20px;
  width: 1px;
  background-color: var(--border-color);
  border-radius: 2px;
  z-index: 1;
}

/* –û–¥–∏–Ω –¥–µ–Ω—å –Ω–µ–¥–µ–ª–∏ ‚Äî —Ç–µ–ø–µ—Ä—å —Å –≥–∏–±–∫–æ–π –≤—ã—Å–æ—Ç–æ–π –∏ –ø–∞–¥–¥–∏–Ω–≥–∞–º–∏ */
.day-row {
  position: relative;
  min-height: var(--day-row-min-height);
  padding: var(--day-row-padding-vertical) 1rem var(--day-row-padding-vertical) 0.75rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap);
  cursor: pointer;
  box-sizing: border-box;
}

/* –ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–∞—è –ª–∏–Ω–∏—è */
.day-row::after {
  content: '';
  position: absolute;
  left: calc(var(--day-left-width) + var(--gap));
  right: 1rem;
  bottom: 0;
  height: 1px;
  background-color: var(--border-color);
  border-radius: 1px;
  z-index: 1;
}

.day-row:last-child::after {
  display: none;
}

/* –õ–µ–≤–∞—è —á–∞—Å—Ç—å —Å –¥–Ω—ë–º –∏ –¥–∞—Ç–æ–π */
.day-left {
  flex: 0 0 var(--day-left-width);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2px;
  user-select: none;
  padding-left: 6px;
  box-sizing: border-box;
  text-align: center;
}

/* –ù–∞–∑–≤–∞–Ω–∏–µ –¥–Ω—è */
.day-name {
  color: #888;
  font-weight: 600;
  font-size: 0.75rem;
  line-height: 1.2;
  padding: 0;
  text-align: center;
  user-select: none;
}

/* –î–∞—Ç–∞ –ø–æ–¥ –Ω–∞–∑–≤–∞–Ω–∏–µ–º */
.day-date {
  font-size: 0.8rem;
  color: #ddd;
  line-height: 1.2;
  padding: 0 0.4rem;
  text-align: center;
}

/* –í—ã–¥–µ–ª–µ–Ω–∏–µ —Å–µ–≥–æ–¥–Ω—è—à–Ω–µ–≥–æ –¥–Ω—è */
.current-day .day-date {
  border: 1px solid #fff;
  background-color: #fff;
  border-radius: 50%;
  font-weight: 700;
  color: #000;
  padding: 0.2rem 0.5rem;
}

/* –ü–æ–ª–µ —Å–º–µ–Ω –∏ —Å–æ–±—ã—Ç–∏–π */
.day-events {
  flex-grow: 1;
  margin-left: calc(var(--day-left-width) * -0.6);
  padding-left: calc(var(--day-left-width) + var(--gap));
  color: #777;
  font-size: 0.85rem;
  user-select: none;
  min-height: 40px;
  border-radius: 0.75rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  box-sizing: border-box;
  display: flex;
  align-items: center;
}

/* –ú–æ–¥–∞–ª–∫–∞ –≤—ã–±–æ—Ä–∞ —Å–º–µ–Ω—ã/—Å–æ–±—ã—Ç–∏—è */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  z-index: 3000;
  pointer-events: auto;
  user-select: none;
}

.choice-modal {
  margin-top: 2.8rem;
  position: absolute;
  background: #eee;
  color: #333;
  border-radius: 1rem;
  padding: 1.2rem 0.75rem 1rem 0.75rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  box-shadow: 0 0 12px #00000033;
  min-width: 280px;
  max-width: 90vw;
  user-select: none;
  pointer-events: auto;
  z-index: 9999;
}

.choice-icon {
  position: absolute;
  top: -1rem;
  left: 1rem;
  font-size: 1.25rem;
  padding: 0.25rem;
  color: #eee;
  z-index: 2;
}

.choice-btn {
  flex: 1;
  background: transparent;
  border: none;
  font-size: 1rem;
  display: flex;
  align-items: center;
  gap: 0.4rem;
  cursor: pointer;
  padding: 0.5rem 1rem;
  border-radius: 0.75rem;
  transition: background-color 0.3s ease;
}

.choice-btn:hover {
  background-color: #ddd;
}

.plus {
  font-weight: 900;
  font-size: 0.75rem;
  line-height: 1;
}

.choice-divider {
  width: 1px;
  height: 1.75rem;
  background: #bbb;
}

/* –ò–∫–æ–Ω–∫–∏ —Å–æ–ª–Ω—Ü–∞ */
.sun-icon-wrapper {
  position: relative;
  font-size: 1.5rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #fcd34d;
}

.sun-icon {
  font-size: 2rem;
  filter: drop-shadow(0 0 4px rgba(255, 255, 255, 0.5));
}

.sun-day-number {
  position: absolute;
  font-size: 0.75rem;
  font-weight: bold;
  color: #000;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}

/* –ê–∫—Ç–∏–≤–Ω—ã–π –¥–µ–Ω—å –≤—ã–¥–µ–ª–µ–Ω–Ω—ã–π (–ø—Ä–∏ –∫–ª–∏–∫–µ) */
.selected-day {
  background-color: rgba(255 255 255 / 0.15);
  border-radius: 0.75rem;
}

/* –ú–µ–¥–∏–∞-–∑–∞–ø—Ä–æ—Å—ã –¥–ª—è –º–∞–ª–µ–Ω—å–∫–∏—Ö —ç–∫—Ä–∞–Ω–æ–≤ */
@media (max-height: 600px), (max-width: 400px) {
  .week-container {
    max-height: calc(100vh - 60px);
    padding: 0 0.75rem 0.75rem;
  }

  .day-row {
    min-height: 40px;
    padding: 6px 0.75rem;
  }

  .day-name,
  .day-date {
    font-size: 0.7rem;
  }

  .choice-modal {
    min-width: 240px;
  }
}
